<style scoped lang="scss">
.warn {
  align-self: center;
  font-size: 24px;
  padding: 12px 16px;
  border-radius: 8px;
  margin-top: 24px;
  margin-bottom: 24px;
  cursor: pointer;
}
.error {
  background-color: $red;
}
.warning {
  background-color: $yellow;
  color: $neutrals1;
  font-weight: 500;
}

</style>
<template>
  <div>
    <div class="warn" @click="fixWarning" :class="errorType">
      {{ errorMessage }}
    </div>
  </div>
</template>
<script>
export default {
  computed: {
    errorShown() {
      return this.$store.state.errorShown;
    },
    errorType() {
      return this.$store.state.errorType;
    },
    errorMessage() {
      return this.$store.state.errorMessage;
    },
  },
  methods: {
    fixWarning() {
      if (this.errorMessage == "Not connected to boba, click to swap!"){
        this.addArbitrum()
      }
    },
    async addArbitrum() {
      try {
        await window.web3.send("wallet_switchEthereumChain", [
          {
            chainId: "0x" + "120".toString(16),
          },
        ]);
      } catch (switchError) {
        if (switchError.code === 4902) {
          try {
            window.web3.send("wallet_addEthereumChain", [
              {
                chainId: "0x" + "120".toString(16),
                chainName: "Boba L2",
                nativeCurrency: {
                  name: "Ether",
                  symbol: "ETH",
                  decimals: 18,
                },
                rpcUrls: ["https://mainnet.boba.network/"],
                blockExplorerUrls: ["https://blockexplorer.boba.network/"],
              },
            ]);
          } catch (addError) {
            console.log(addError);
          }
        }
      }
    },
  },
};
</script>