<style scoped lang="scss">
.bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba($color: #000000, $alpha: 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal {
  border: 2px solid $neutrals2;
  padding: 32px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  background-color: $neutrals1;
}
.modal-row {
  padding: 8px 16px;
  cursor: pointer;
  transition: all 0.2s;
  border-radius: 8px;
  font-size: 36px;
  display: flex;
  align-items: center;
  &:hover {
    background-color: $neutrals2;
  }

  padding: 16px;
}

img {
  height: 40px;
  width: 40px;
  margin-right: 24px;
}
</style>

<template>
  <div class="bg" @click="closeModal">
    <div class="modal" @click.stop>
      <div class="modal-row" @click="connectMetamask">
        <img src="@/assets/metamask.svg" alt="" />
        <div>Metamask</div>
      </div>
      <div class="modal-row" @click="connectWalletConnect">
        <img src="@/assets/walletconnect.svg" alt="" />
        <div>WalletConnect</div>
      </div>
      <div class="modal-row" @click="connectCoinbase">
        <img src="@/assets/coinbase.svg" alt="" />
        <div>Coinbase Wallet</div>
      </div>
    </div>
  </div>
</template>
<script>
import { providers } from "ethers";
import WalletConnectProvider from "@walletconnect/web3-provider";
import WalletLink from "walletlink";
export default {
  methods: {
    closeModal() {
      this.$store.commit("setWalletConnectModalShown", false);
    },

    async connectMetamask() {
      let provider = window.ethereum;
      window.web3 = new providers.Web3Provider(provider);
      await window.web3.send("eth_requestAccounts", []);
      this.$store.dispatch("setWalletData");
      this.$store.commit("setConnectModal", false);
    },
    async connectWalletConnect() {
      const provider = new WalletConnectProvider({
        infuraId: "9e63d02ca12d426ab9de1339f9de9340",
      });
      await provider.enable();
      window.web3 = new providers.Web3Provider(provider);
      await window.web3.listAccounts();

      this.$store.dispatch("setWalletData");
      this.$store.commit("setConnectModal", false);
    },
    async connectCoinbase() {
      const walletLink = new WalletLink({
        appName: "Hermetica",
        appLogoURI: "www.google.com",
        darkMode: true,
      });
      let provider = walletLink.makeWeb3Provider(
        `https://mainnet.infura.io/v3/${"9e63d02ca12d426ab9de1339f9de9340"}`,
        1
      );
      window.web3 = new providers.Web3Provider(provider);

      await window.web3.send("eth_requestAccounts", []);
      this.$store.dispatch("setWalletData");
      this.$store.commit("setConnectModal", false);
    },
  },
};
</script>