
import { ethers, BigNumber } from "ethers";

import CARBON_POOL_ABI from "@/abi/CarbonRewards.json";
import ERC20_ABI from "@/abi/ERC20.json";
import SUSHI_FACTORY_ABI from "@/abi/SushiFactory.json";
import SUSHI_PAIR_ABI from "@/abi/SushiPair.json";



const USDC_ADDRESS = `0x66a2A913e447d6b4BF33EFbec43aAeF87890FBbc`
const WETH_ADDRESS = `0xDeadDeAddeAddEAddeadDEaDDEAdDeaDDeAD0000`
const ETH_ADDRESS = `0x0000000000000000000000000000000000000000`

//currently nyan
const CARBON_ADDRESS = `0x9AB1F99BaEFD4F809B971D1F84A93096c346DD16`

const USDC_DECIMALS = 6
const WETH_DECIMALS = 18


const ETH_POOL_CONTRACT_ADDRESS = `0xC1123908933aA8E4998cAefEf87FCae67c2C85Cf`
const ETH_CARBON_LP_POOL_CONTRACT_ADDRESS = `0x2b87E92dA50e3ABc4aFebCD25bc567074778A3D3`
//const CARBON_POOL_CONTRACT_ADDRESS = `0x2C5058325373d02Dfd6c08E48d91FcAf8fD49f45`
//const NYAN_CARBON_CONTRACT_ADDRESS = `0x45acd6Af27B2506ad68C0fEA9F597D6eE6818722`

const SUSHI_ETH_CARBON_LP_CONTRACT_ADDRESS = `0xA5447642cb156C4459D9eeB86A552ad55b51509e`

// const SUSHI_ROUTER_CONTRACT_ADDRESS = `0x1b02dA8Cb0d097eB8D57A175b88c7D8b47997506`
const SUSHI_FACTORY_CONTRACT_ADDRESS = `0xC0cD4c9851B18DbfcBcF474b9e674fBF4639bD2c`
//const CARBON_POOL_LOCK_CONTRACT_ADDRESS = `0x0BcC628397Ca51562d85376C35eD016968890Ac3`

const SECONDS_IN_DAY = BigNumber.from("86400");

const CARBON_INITIAL_SUPPLY = BigNumber.from(100_000_000)

const CONTRACTS = [
    ETH_POOL_CONTRACT_ADDRESS,
    ETH_CARBON_LP_POOL_CONTRACT_ADDRESS,
   /*  CARBON_POOL_CONTRACT_ADDRESS,
    NYAN_CARBON_CONTRACT_ADDRESS,
    CARBON_POOL_LOCK_CONTRACT_ADDRESS */
];


async function getPairAddress(token0, token1) {
console.log("getting pair");
    let SushiFactory = new ethers.Contract(
        SUSHI_FACTORY_CONTRACT_ADDRESS,
        SUSHI_FACTORY_ABI,
        window.web3
    );
    console.log(`fac ${SushiFactory}`);
    let pairAddress = await SushiFactory.getPair(token0, token1);
    console.log(`pairad ${pairAddress}`)
    return pairAddress;
}


export default {
    state: {
        circulatingSupply: 0,
        ETHPrice: 0,
        CarbonPrice: 0,
        TVL: 0,
        farmInfo: {}


    },
    getters: {
        displayMarketCap(state) {
            if (state.CarbonPrice && state.circulatingSupply) {
                return parseInt(state.CarbonPrice.mul(state.circulatingSupply).mul(state.ETHPrice).div(BigNumber.from(10).pow(18))).toLocaleString()
            }
            return 0
        },

        TVLInUSD(state) {
            if (state.TVL && state.ETHPrice) {
                return parseInt(state.TVL.mul(state.ETHPrice).div(BigNumber.from(10).pow(18))).toLocaleString();
            }
            return 0
        },

        displayCirculatingSupply(state) {
            return parseInt(state.circulatingSupply).toLocaleString();
        },
        displayTVL(state) {
            let string = ethers.utils.formatUnits(state.TVL, 18)

            return string.substring(0, string.indexOf(".") + 2)
        },
        displayCarbonPriceInUSD(state) {
            if (state.CarbonPrice && state.ETHPrice) {
                let formattedString = ethers.utils.formatUnits(state.ETHPrice.mul(state.CarbonPrice).toString(), 18)
                return formattedString.substr(0, formattedString.indexOf(".") + 4)
            }
            return ""
        }
    },
    actions: {
        async calculateCirculatingSupply({ commit }) {

            let total = BigNumber.from(1_500_000)
            for (let contract of CONTRACTS) {
                let poolContract = new ethers.Contract(contract, CARBON_POOL_ABI, window.web3);

                let periodFinish = await poolContract.periodFinish();
                let rewardRate = await poolContract.rewardRate();
                let now = Math.round(Date.now() / 1000);
                total = total.add(
                    rewardRate.mul(periodFinish.sub(now)).div(BigNumber.from(10).pow(18))
                );
            }
            commit("setCirculatingSupply", CARBON_INITIAL_SUPPLY.sub(total));
        },

        async getETHPrice({ commit }) {
            console.log("getting eth price");
            let sushiPairAddress = await getPairAddress(WETH_ADDRESS, USDC_ADDRESS)
            let sushiPairContract = new ethers.Contract(
                sushiPairAddress,
                SUSHI_PAIR_ABI,
                window.web3
            );
                console.log(`contract`);
            let rewardToken0 = await sushiPairContract.token0();
            console.log(`rewardToken0 ${rewardToken0}`);
            let rewardReserves = await sushiPairContract.getReserves();
            let rewardTokenReserves =
                rewardToken0 !== WETH_ADDRESS
                    ? rewardReserves._reserve1
                    : rewardReserves._reserve0;
            let WETHReserves =
                rewardToken0 !== WETH_ADDRESS
                    ? rewardReserves._reserve0
                    : rewardReserves._reserve1;
            let rewardNumerator = rewardTokenReserves.mul(BigNumber.from(10).pow(WETH_DECIMALS));
            let rewardDenominator = WETHReserves.sub(1);
            let price = rewardNumerator.div(rewardDenominator).div(BigNumber.from(10).pow(USDC_DECIMALS));
            commit("setETHPrice", price);
        },


        async getCarbonPrice({ commit }) {
            let carbonPair = await getPairAddress(WETH_ADDRESS, CARBON_ADDRESS)
console.log("getting carbong price");

            let carbonPairContract = new ethers.Contract(
                carbonPair,
                SUSHI_PAIR_ABI,
                window.web3
            );
            let rewardToken0 = await carbonPairContract.token0();
            let rewardReserves = await carbonPairContract.getReserves();
            let rewardTokenReserves =
                rewardToken0 === WETH_ADDRESS
                    ? rewardReserves._reserve1
                    : rewardReserves._reserve0;
            let WETHReserves =
                rewardToken0 === WETH_ADDRESS
                    ? rewardReserves._reserve0
                    : rewardReserves._reserve1;
            let decimals = BigNumber.from(1);
            let rewardNumerator = rewardTokenReserves
                .mul(decimals)
                .mul(BigNumber.from(10).pow(18));
            let rewardDenominator = WETHReserves.sub(decimals);
            let rewardPrice = rewardNumerator.div(rewardDenominator);
            commit("setCarbonPrice", rewardPrice);

        },

        async getTVL({ commit, state }) {
            let TVL = BigNumber.from(0)
            for (let farm of Object.values(state.farmInfo)) {
                TVL = TVL.add(farm.valueOfTotalStaked)
            }
            commit("setTVL", TVL);

        },

        async getFarmInfo({ commit, rootState }, address) {
            let poolContract = new ethers.Contract(
                address,
                CARBON_POOL_ABI,
                window.web3
            );
            let rewardToken = await poolContract.rewardToken();
            console.log(`got rewardToken ${rewardToken}`);

            let rewardContract = new ethers.Contract(
                rewardToken,
                ERC20_ABI,
                window.web3
            );

            let rewardName = await rewardContract.symbol();
            let stakedToken = await poolContract.stakedToken();
            let stakedName;
            let stakedContract = new ethers.Contract(
                stakedToken,
                ERC20_ABI,
                window.web3
            );
            if (stakedToken != ETH_ADDRESS) {
                stakedName = await stakedContract.name();
            } else {
                stakedName = "ETH";
            }

            let rewardRate = await poolContract.rewardRate();
            let totalSupply = await poolContract.totalSupply();
            let depositedByUser = 0
            let rewardsEarned = 0
            let stakeTokenBalance = 0
            let rewardTokenBalance = 0
            let approvedAmount = BigNumber.from(0)
            if (rootState.connectedAddress) {
                rewardTokenBalance = await rewardContract.balanceOf(rootState.connectedAddress)
                if (stakedToken != ETH_ADDRESS) {
                    stakedName = await stakedContract.name();
                    stakeTokenBalance = await stakedContract.balanceOf(rootState.connectedAddress)
                    approvedAmount = await stakedContract.allowance(rootState.connectedAddress, address);
                } else {
                    approvedAmount = BigNumber.from(1)
                    stakeTokenBalance = await window.web3.getBalance(rootState.connectedAddress)
                }
                depositedByUser = await poolContract.balanceOf(rootState.connectedAddress);
                rewardsEarned = await poolContract.earned(rootState.connectedAddress);
            }

            //APR calculations
            let rewardPerDayInTokens = rewardRate.mul(SECONDS_IN_DAY);
            let valueOfDailyReward = rewardPerDayInTokens.mul(rootState.farm.CarbonPrice);
            let valueOfTotalStaked;
            if (stakedToken === ETH_ADDRESS) {
                valueOfTotalStaked = totalSupply;
            }
            if (stakedToken === SUSHI_ETH_CARBON_LP_CONTRACT_ADDRESS) {
                let CarbonToken = new ethers.Contract(
                    rewardToken,
                    ERC20_ABI,
                    window.web3
                );
                let carbonLPBalance = await CarbonToken.balanceOf(stakedToken);
                valueOfTotalStaked = carbonLPBalance
                    .mul(2)
                    .mul(rootState.farm.CarbonPrice)
                    .div(BigNumber.from(10).pow(18));
            }
            if (stakedToken == CARBON_ADDRESS) {
                valueOfTotalStaked = totalSupply
                    .mul(rootState.farm.CarbonPrice)
                    .div(BigNumber.from(10).pow(18));
            }

            if (stakedToken == "0x9AB1F99BaEFD4F809B971D1F84A93096c346DD16") {
                let CarbonToken = new ethers.Contract(
                    rewardToken,
                    ERC20_ABI,
                    window.web3
                );
                let carbonLPBalance = await CarbonToken.balanceOf(stakedToken);
                valueOfTotalStaked = carbonLPBalance
                    .mul(2)
                    .mul(rootState.farm.CarbonPrice)
                    .div(BigNumber.from(10).pow(18));
            }
            let dailyROI = valueOfDailyReward.div((valueOfTotalStaked != 0 && valueOfTotalStaked && parseInt(valueOfTotalStaked) > 0) ? valueOfTotalStaked : 1);
            let APR = ethers.utils.formatUnits(dailyROI.mul(365), 16);

            let farmInfo = {
                address,
                stakedToken,
                rewardToken,
                stakedName,
                rewardName,
                rewardRate,
                totalSupply,
                valueOfTotalStaked,
                APR,
                depositedByUser,
                rewardsEarned,
                stakeTokenBalance,
                rewardTokenBalance,
                approvedAmount
            }
            commit("setFarmInfo", farmInfo)
            return true

        }
    },
    mutations: {
        setCirculatingSupply(state, supply) {
            state.circulatingSupply = supply;
        },
        setETHPrice(state, price) {
            state.ETHPrice = price;
        },
        setCarbonPrice(state, price) {
            state.CarbonPrice = price;
        }
        ,
        setTVL(state, TVL) {
            state.TVL = TVL;
        }
        ,
        setFarmInfo(state, farmInfo) {
            state.farmInfo[farmInfo.address] = farmInfo;
        }
    }
}