<script>
import Button from "@/components/ui/Button.vue";
export default {
  components: {
    Button,
  },
  methods: {
    async connectWallet() {
      this.$store.commit("setWalletConnectModalShown", true);
    },
  },
  computed: {
    route() {
      return this.$route.name;
    },
    shortenedAddress() {
      return (
        this.connectedAddress.substring(0, 6) +
        "..." +
        this.connectedAddress.substring(this.connectedAddress.length - 4)
      );
    },
    connectedAddress() {
      return this.$store.state.connectedAddress;
    },
  },
};
