// import { ethers } from "ethers";
import { createStore } from "vuex";
const ETH_POOL_CONTRACT_ADDRESS = `0xC1123908933aA8E4998cAefEf87FCae67c2C85Cf`
const ETH_CARBON_LP_POOL_CONTRACT_ADDRESS = `0x2b87E92dA50e3ABc4aFebCD25bc567074778A3D3`
// const CARBON_POOL_CONTRACT_ADDRESS = `0x2C5058325373d02Dfd6c08E48d91FcAf8fD49f45`
// const NYAN_CARBON_CONTRACT_ADDRESS = `0x45acd6Af27B2506ad68C0fEA9F597D6eE6818722`
// const CARBON_POOL_LOCK_CONTRACT_ADDRESS = `0x0BcC628397Ca51562d85376C35eD016968890Ac3`
const CONTRACTS = [
  ETH_POOL_CONTRACT_ADDRESS,
  ETH_CARBON_LP_POOL_CONTRACT_ADDRESS,
 /*  CARBON_POOL_CONTRACT_ADDRESS,
  NYAN_CARBON_CONTRACT_ADDRESS,
  CARBON_POOL_LOCK_CONTRACT_ADDRESS */
];


import farm from "./farm"

export default createStore({
  state: {
    connectedAddress: null,
    connectedChain: null,
    errorMessage: "",
    errorShown: false,
    errorType: "", //"", "warning", "error"
    walletConnectModalShown: false,
    farmInfoLoaded: false,
  },
  getters: {
    connectedToArbitrum: (state) => {
      return state.connectedChain == 42161;
    },
    isConnected: (state) => {
      return state.connectedAddress != null;
    }
  },
  actions: {
    async refreshData({ dispatch }) {
      dispatch("calculateCirculatingSupply");
     // dispatch("getETHPrice");
     // await dispatch("getCarbonPrice");

      await Promise.all(CONTRACTS.map(async (x) => await dispatch("getFarmInfo", x)));
     // dispatch("getTVL");
      return true

    },


    async setWalletData({ commit, dispatch }) {
      const accounts = await window.web3.listAccounts();
      if (accounts.length > 0) {
        commit("setAddress", accounts[0]);
      }

      const chain = await window.web3.getNetwork();
      commit("setChain", chain.chainId);

      if (chain.chainId != 288) {
        commit("setError", {
          message: "Not connected to boba, click to swap!",
          type: "warning",
        });
      } else {
        await dispatch("refreshData");
        commit("setFarmInfoLoaded", true)
        setInterval(async () => {
          dispatch("refreshData");

        }, 10000);
      }

    }
  },

  mutations: {
    setFarmInfoLoaded(state, value) {
      state.farmInfoLoaded = value
    },
    setAddress(state, address) {
      state.connectedAddress = address;
    },
    setChain(state, chain) {
      state.connectedChain = chain;
    },
    setError(state, { message, type }) {
      state.errorMessage = message;
      state.errorType = type;
      state.errorShown = true;
    },
    clearError(state) {
      state.errorShown = false;
      state.errorMessage = "";
    },
    setWalletConnectModalShown(state, value) {
      state.walletConnectModalShown = value;
    }
  },
  modules: { farm }
});
