import { createRouter, createWebHistory } from "vue-router";
const Farm = () => import(/* webpackPrefetch: true */ "../views/Farm.vue");
const Info = () => import(/* webpackPrefetch: true */ "../views/Info.vue");
const routes = [
  {
    path: "/",
    name: "Farm",
    component: Farm
  },

  {
    path: "/info",
    name: "Info",
    component: Info
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
