<template>
  <TheNavbar></TheNavbar>
  <ErrorMessage v-if="errorShown"></ErrorMessage>
  <div class="main">
    <router-view />
  </div>
  <TheFooter></TheFooter>
  <WalletModal v-if="walletConnectModalShown"></WalletModal>
</template>
<script>
import { providers } from "ethers";
import detectEthereumProvider from "@metamask/detect-provider";
import TheNavbar from "@/components/TheNavbar.vue";
import ErrorMessage from "@/components/ErrorMessage.vue";
import TheFooter from "@/components/TheFooter.vue";
import WalletModal from "@/components/WalletModal.vue";
export default {
  components: {
    TheNavbar,
    ErrorMessage,
    TheFooter,
    WalletModal,
  },
  computed: {
    errorShown() {
      return this.$store.state.errorShown;
    },
    walletConnectModalShown() {
      return this.$store.state.walletConnectModalShown;
    },
  },
  created() {
    (async () => {
      let provider = await detectEthereumProvider();
      if (provider != null) {
        provider = new providers.Web3Provider(provider);
        window.web3 = provider;
        this.$store.dispatch("setWalletData");

        provider.provider.on("accountsChanged", async () => {
          window.location.reload();
        });
        provider.provider.on("chainChanged", async () => {
          window.location.reload();
        });
      } else {
        this.$store.commit("setError", {
          message: "No wallet detected, please download metamask",
          type: "error",
        });
      }
    })();
  },
};
</script>
